<template>
  <div class="loader" v-show="isShown">
    <div class="loader__spin"></div>
    <div class="loader__bg"></div>
    <div class="loader__text">{{ text }}</div>
  </div>
</template>
<style lang="less" src="./Loader.less"></style>

<script>
  export default {
    props: {
      shown: Boolean,
      text: String,
    },
    data() {
      return {
        isShown: this.shown,
        message: this.text,
      };
    },
    methods: {
      show(text = '') {
        this.isShown = true;
        this.message = text;
      },
      hide() {
        this.isShown = false;
      },
    },
  }
</script>
