<template src="./SettingsPage.html"></template>
<style lang="less" src="./SettingsPage.less"></style>

<script>
  import SimpleLayout from '../../layouts/simple/SimpleLayout.vue';
  import { Registry } from '../../Registry';
  import { PATH } from '../../routes';
  import { SERVICES } from '../../services';
  import { GOOGLE_EVENTS } from '../../googleEvents';
  import ToggleButton from '../../components/buttons/toggle-button/ToggleButton.vue';
  import SelectionList from '../../components/selection-list/SelectionList.vue';
// import SelectLocation from '../../components/select-location/SelectLocation.vue';
  import { callExternalEvent, noop } from '../../helpers';
  import { ContentEvents } from '../../../bg/ContentEvents';
  import { FeatureTypes } from '../../../bg/FeatureTypes';
  import { HISTORY_CLEAN_TYPE } from '../../../bg/browser-cleaner/services/historyCleaner/HistoryCleanerTypes';
  import { CACHE_CLEAN_TYPE } from '../../../bg/browser-cleaner/services/cacheCleaner/CacheCleanerTypes';

  const registry = Registry.getInstance();
  let dispatcher = null;

  const historyCleanOptions = [
/*
    {
      id: HISTORY_CLEAN_TYPE.WHEN_LEAVE,
      title: 'When leave website'
    },
*/
    {
      id: HISTORY_CLEAN_TYPE.EVERY_DAY,
      title: 'Every day'
    },
    {
      id: HISTORY_CLEAN_TYPE.EVERY_HOUR,
      title: 'Every hour'
    }
  ];

  const cacheCleanOptions = [
/*
    {
      id: CACHE_CLEAN_TYPE.WHEN_LEAVE,
      title: 'When leave website'
    },
*/
    {
      id: CACHE_CLEAN_TYPE.EVERY_DAY,
      title: 'Every day'
    },
    {
      id: CACHE_CLEAN_TYPE.EVERY_HOUR,
      title: 'Every hour'
    }
  ];

  export default {
    data() {
      return {
        historyCleanOptions,
        cacheCleanOptions,
        historyCleanerOptionValue: undefined,
        cacheCleanerOptionValue: undefined,
        featureEnabled: {},
        cacheCleanerDisabled: false,
        historyCleanerDisabled: false
      }
    },
    components: {
      'simple-layout': SimpleLayout,
      'toggle-button': ToggleButton,
      'selection-list': SelectionList
//    'select-location': SelectLocation,
    },
    created() {
      dispatcher = registry.get(SERVICES.EXTERNAL_DISPATCHER);
    },
    async mounted() {
      const self = this;

      this.$refs.layout.showCloseButton();
      this.trackEvent(GOOGLE_EVENTS.SETTINGS_PAGE__SHOWN);

      const state = await callExternalEvent(dispatcher, ContentEvents.POPUP_GET_PROTECTION_STATE);
      let $ref;

      state.features.forEach(feature => {
        this.featureEnabled[feature.name] = !!feature.enabled;

        this.updateSubItemsDisableState();

        switch (feature.name) {
          case FeatureTypes.AD_BLOCKER:
            $ref = this.$refs['toggle_AD_BLOCKER'];
            feature.enabled ? $ref.turnON() : $ref.turnOFF();
            break;

          case FeatureTypes.ANTIMALWARE:
            $ref = this.$refs['toggle_ANTIMALWARE'];
            feature.enabled ? $ref.turnON() : $ref.turnOFF();
            break;

          case FeatureTypes.ANALYTICS_BLOCKER:
            $ref = this.$refs['toggle_ANALYTICS_BLOCKER'];
            feature.enabled ? $ref.turnON() : $ref.turnOFF();
            break;

          case FeatureTypes.HISTORY_CLEANER:
            $ref = this.$refs['toggle_HISTORY_CLEANER'];
            feature.enabled ? $ref.turnON() : $ref.turnOFF();
            self.historyCleanerOptionValue = feature.props.type;
            break;

          case FeatureTypes.CACHE_CLEANER:
            $ref = this.$refs['toggle_CACHE_CLEANER'];
            feature.enabled ? $ref.turnON() : $ref.turnOFF();
            self.cacheCleanerOptionValue = feature.props.type;
            break;
        }
      });
    },
    methods: {
      async enableFeature(featureName) {
        await callExternalEvent(dispatcher, ContentEvents.POPUP_SET_PROTECTION_STATE, {
          action: 'SET_FEATURE_STATE',
          name: featureName,
          enabled: true
        });

        this.featureEnabled[featureName] = true;

        this.updateSubItemsDisableState();

        this.trackEvent(GOOGLE_EVENTS[featureName + '__ON']);
      },

      async disableFeature(featureName) {
        await callExternalEvent(dispatcher, ContentEvents.POPUP_SET_PROTECTION_STATE, {
          action: 'SET_FEATURE_STATE',
          name: featureName,
          enabled: false
        });

        this.featureEnabled[featureName] = false;

        this.updateSubItemsDisableState();

        this.trackEvent(GOOGLE_EVENTS[featureName + '__OFF']);
      },

      async setFeatureProp(featureName, prop, value) {
        await callExternalEvent(dispatcher, ContentEvents.POPUP_SET_PROTECTION_STATE, {
          action: 'SET_FEATURE_PROPS',
          name: featureName,
          props: {
            [prop]: value
          }
        });

        const event = GOOGLE_EVENTS[featureName + '__PROP'];
        event.label = `${prop}=${value}`;
        this.trackEvent(event);
      },

      updateSubItemsDisableState() {
        this.cacheCleanerDisabled = !this.featureEnabled[FeatureTypes.CACHE_CLEANER];
        this.historyCleanerDisabled = !this.featureEnabled[FeatureTypes.HISTORY_CLEANER];
      },


      onClosePage() {
        this.$router.push(PATH.MAIN, noop, noop);
      },

      noop
    },
/*
      async onLocationSelected(location) {
        const event = GOOGLE_EVENTS.LOCATION__SELECTED;
        event.label = location.name;
        this.trackEvent(event);
        this.currentLocation = location;

        if (!this.isShieldDisabled) {
          this.$refs.template.loader().show(PROCESSING);
          await urbanLocationService.activateLocationByCode(this.currentLocation.code);
          this.updateIpInfo();
          this.$refs.template.loader().hide();
        }
      },

      onSearchFocus() {
        this.trackEvent(GOOGLE_EVENTS.SEARCH_INPUT__FOCUSED);
      },

      onSearchClear() {
        this.trackEvent(GOOGLE_EVENTS.SEARCH_INPUT__CLEAR);
      },
*/

}
</script>