import '../panel-os/panelos-client';

import { Registry } from './Registry';
import { SERVICES } from './services';

const register = Registry.getInstance();

export class PanelOS {
  getAdCounters() {
    return new Promise((resolve) => {
      PanelOSClient.GetAdBlockerCounters((response) => resolve(response));
    });
  }

  isDisabled() {
    return new Promise((resolve, reject) => {
      PanelOSClient.GetAdBlockerStatus((response) => {
        resolve(response.DISPLAY === 'disabled');
      });
    });
  }

  enable() {
    const bg = register.get(SERVICES.BACKGROUND_PAGE);
    bg.EnableAdBlocker((status)=>{}, (error)=>{}, ['DISPLAY']);
  }

  disable() {
    const bg = register.get(SERVICES.BACKGROUND_PAGE);
    bg.DisableAdBlocker((status)=>{}, (error)=>{}, ['DISPLAY']);
  }
}