<template>
  <div class="toggle_button "
       :class="{ 'toggle_button--off': !isON, 'toggle_button--on': isON }"
       @click="onClick"
  >
  </div>
</template>
<style lang="less" src="./ToggleButton.less"></style>

<script>
  const EVENTS = {
    SWITCH_TURN_ON: 'on',
    SWITCH_TURN_OFF: 'off',
    CLICKED: 'click'
  };

  export default {
    data() {
      return {
        isON: true,
        disabled: false
      };
    },
    methods: {
      onClick() {
        this.$emit(EVENTS.CLICKED);

        if (this.disabled) {
          return;
        }

        this.isON = !this.isON;
        if (this.isON) {
          this.$emit(EVENTS.SWITCH_TURN_ON);
        }
        else {
          this.$emit(EVENTS.SWITCH_TURN_OFF);
        }
      },
      turnON() {
        this.isON = true;
      },
      turnOFF() {
        this.isON = false;
      },
      enable() {
        this.disabled = false;
      },
      disable() {
        this.disabled = true;
      }
    }
  }
</script>