import { PanelOS } from './PanelOS';
import { ContentDispatcherService } from '@urbandevs/mario-core/dist/src';
import { TabDispatcher } from '@urbandevs/mario-core/dist/src/Packages/Tab/Content/TabDispatcher';
import { GoogleAnalyticsContentDispatcher } from '@urbandevs/mario-google-analytics/dist/GoogleAnalyticsContentDispatcher';
import { Registry } from './Registry';
import { SERVICES } from './services';

const register = Registry.getInstance();

const dispatcherService = new ContentDispatcherService();
const googleAnalytics = new GoogleAnalyticsContentDispatcher(dispatcherService);
const tabDispatcher = new TabDispatcher(dispatcherService);

register.set(SERVICES.PANEL_OS, new PanelOS());
register.set(SERVICES.EXTERNAL_DISPATCHER, dispatcherService);
register.set(SERVICES.TAB_SERVICE, tabDispatcher);
register.set(SERVICES.GOOGLE_ANALYTICS, googleAnalytics);

