import { MarioEvent } from '@urbandevs/mario-core/dist/src';

export function isChrome() {
  return process.env.BROWSER === 'CHROME_BROWSER';
}

export function isBugsnagEnabled() {
  return Boolean(+process.env.BUGSNAG_IS_ENABLED);
}

export function noop() {}

export async function callExternalEvent(dispatcher, eventName, params) {
  return new Promise(resolve => {
    dispatcher.emit(new MarioEvent(eventName, params), (r) => resolve(r));
  });
}

export async function delay(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}
