<template src="./InterruptionPage.html"></template>
<style lang="less" src="./InterruptionPage.less"></style>

<script>
  import SimpleLayout from '../../layouts/simple/SimpleLayout.vue';
  import { Registry } from '../../Registry';
  import { PATH } from '../../routes';
  import { SERVICES } from '../../services';
  import { GOOGLE_EVENTS } from '../../googleEvents';

  let managementService = null;
  let interruptionService = null;

  export default {
    components: {
      'simple-layout': SimpleLayout,
    },
    data() {
      return {
        browser: process.env.BROWSER_NAME
      }
    },
    beforeCreate() {
      managementService = Registry.getInstance().get(SERVICES.MANAGEMENT);
      interruptionService = Registry.getInstance().get(SERVICES.INTERRUPTION_SERVICE);
    },
    methods: {
      async onDisableOtherExtension() {
        this.trackEvent(GOOGLE_EVENTS.DISABLE_OTHER_EXTENSION_BUTTON__CLICKED);
        await interruptionService.disableExtensionsThatCauseInterruptions();
        return this.$router.push(PATH.MAIN);
      },
      hoverDisableOtherExtension() {
        this.trackEvent(GOOGLE_EVENTS.DISABLE_OTHER_EXTENSION_BUTTON__HOVER);
      },
    }
  }
</script>