<template src="./AnnouncementPage.html"></template>
<style lang="less" src="./AnnouncementPage.less"></style>

<script>
  import { ExtensionMode }  from '../../../bg/services/mode/ExtensionMode';
  import { GOOGLE_EVENTS }  from '../../googleEvents';
  import SimpleLayout       from '../../layouts/simple/SimpleLayout.vue';
  import ProtectionLevelBar from '../../components/protection-level-bar/ProtectionLevelBar.vue';
  import { PATH }           from '../../routes';
  import { SERVICES }       from '../../services';
  import { Registry }       from '../../Registry';
  import { FLAGS }          from '../../flags';
  import { noop }           from '../../helpers';

  let flagService;

  export default {
    components: {
      'simple-layout': SimpleLayout,
      'protection-level-bar': ProtectionLevelBar
    },
    mounted() {
      flagService = Registry.getInstance().get(SERVICES.FLAG_SERVICE);
      this.trackEvent(GOOGLE_EVENTS.ANNOUNCEMENT_PAGE__SHOWN);
    },
    methods: {
      onProxyUnblockerLinkClick() {
        const url = process.env.URBAN_VPN_STORE_URL;
        this.openNewTab('ProxyUnblocker', url)
      },
      async onOkClicked() {
        this.trackEvent(GOOGLE_EVENTS.ANNOUNCEMENT_OK__CLICKED);
        await flagService.activate(FLAGS.ANNOUNCEMENT_V5_0_0_SHOWN);
        await this.$router.push(PATH.MAIN, noop, noop);
      }
    }
  }

</script>