import MainPage from './pages/main/MainPage.vue';
import InitPage from './pages/init/InitPage.vue';
import InterruptionPage from './pages/interruption/InterruptionPage.vue';
import RateUsPage from './pages/rate-us/RateUsPage.vue';
import SettingsPage from './pages/settings/SettingsPage.vue';
import AnnouncementPage from './pages/announcement/AnnouncementPage.vue';
import { Registry } from './Registry';
import { SERVICES } from './services';
import { FLAGS } from './flags';
import { MarioEvent } from '@urbandevs/mario-core/dist/src';
import { ContentEvents } from '../bg/ContentEvents';
import { isChrome, isEdge } from '../bg/libs/helpers';

const register = Registry.getInstance();

const PATH = {
  INITIALISING: '/',
  MAIN: '/main',
  INTERRUPTION: '/interruption',
  RATE_US: '/rate-us',
  SETTINGS: '/settings',
  ANNOUNCEMENT: '/announcement',
};

const routes = [
  {
    path: PATH.MAIN,
    component: MainPage,
    beforeEnter: async (to, from, next) => {
      const extensionModeService = register.get(SERVICES.EXTENSION_MODE);
      const dispatcher = register.get(SERVICES.EXTERNAL_DISPATCHER);
      const pageHistoryService = Registry.getInstance().get(SERVICES.PAGE_HISTORY_SERVICE);
      const interruptionService = register.get(SERVICES.INTERRUPTION_SERVICE);
      const flagService = register.get(SERVICES.FLAG_SERVICE);

      if ((isChrome() || isEdge()) && await interruptionService.isExtensionInterrupted()) {
        next(PATH.INTERRUPTION);
        return;
      }

      const isAnnouncementVisited = await flagService.isActivated(FLAGS.ANNOUNCEMENT_V5_0_0_SHOWN);
      if (!isAnnouncementVisited) {
        next(PATH.ANNOUNCEMENT);
        return;
      }

      const isRateUsNotVisited = !(await pageHistoryService.visited(PATH.RATE_US));
      const getCounter = new Promise((resolve) => {
        dispatcher.emit(new MarioEvent(ContentEvents.POPUP_GET_COUNTER), counter => resolve(counter));
      });
      const counter = await getCounter.then();

      if (counter === 5 && isRateUsNotVisited) {
        next(PATH.RATE_US);
        return;
      }

      next();
    },
  },
  {
    path: PATH.INITIALISING,
    component: InitPage
  },
  {
    path: PATH.INTERRUPTION,
    component: InterruptionPage
  },
  {
    path: PATH.RATE_US,
    component: RateUsPage
  },
  {
    path: PATH.SETTINGS,
    component: SettingsPage
  },
  {
    path: PATH.ANNOUNCEMENT,
    name: PATH.ANNOUNCEMENT,
    component: AnnouncementPage,
  }
];

export { routes, PATH };
