<template>
  <div class="power_button" @click="onClick">
    <div class="power_button--off" v-show="isON"></div>
    <div class="power_button--on" v-show="!isON"></div>
  </div>
</template>
<style lang="less" src="./PowerButton.less"></style>

<script>
  const EVENTS = {
    ON: 'on',
    OFF: 'off',
  };

  export default {
    data() {
      return {
        isON: true
      };
    },
    methods: {
      onClick() {
        this.isON = !this.isON;
        const event = this.isON ? EVENTS.ON : EVENTS.OFF;
        this.$emit(event);
      },
      turnON(fireEvent = true) {
        this.isON = true;
        if (fireEvent) {
          this.$emit(EVENTS.ON);
        }
      },
      turnOFF(fireEvent = true) {
        this.isON = false;
        if (fireEvent) {
          this.$emit(EVENTS.OFF);
        }
      }
    }
  }
</script>
