<template src="./Promotion.html"></template>
<style lang="less" src="./Promotion.less"></style>

<script>
  import { GOOGLE_EVENTS } from '../../googleEvents';

  export default {
    props: ['elements'],
    methods: {
      makeFontStylesClass(element) {
        if (!element.style) {
          return {};
        }

        return {
          'promotion--strong': element.style === 'strong',
          'promotion--italic': element.style === 'italic',
          'promotion--normal': element.style === 'normal',
        }
      },
      onLinkClicked(element) {
        const event = GOOGLE_EVENTS.PROMOTION_LINK_CLICKED;
        event.action = element.text;
        event.label = element.link;
        this.trackEvent(event);
        this.openNewTab(element.text, element.link);
      }
    },
  }
</script>