<template src="./Menu.html"></template>
<style lang="less" src="./Menu.less"></style>

<script>
  import CrossIcon from '../icons/cross-icon/CrossButton.vue';

  const EVENTS = {
    MENU_HIDDEN: 'hidden',
    MENU_SHOWN: 'shown',
  };
  export default {
    props: {
      active: Boolean,
    },
    components: {
      'cross-icon': CrossIcon
    },
    data() {
      return {
        isActiveMenu: this.active,
      };
    },
    methods: {
      show() {
        this.isActiveMenu = true;
        this.$emit(EVENTS.MENU_SHOWN);
      },
      hide() {
        this.isActiveMenu = false;
        this.$emit(EVENTS.MENU_HIDDEN);
      },
      toggle() {
        this.isActiveMenu = !this.isActiveMenu;
        const event = this.isActiveMenu ? EVENTS.MENU_SHOWN : EVENTS.MENU_HIDDEN;
        this.$emit(event);
      },
    }
  }
</script>