<template src="./MainLayout.html"></template>
<style lang="less" src="./MainLayout.less"></style>

<script>
  import { Registry } from '../../Registry';
  import { SERVICES } from '../../services';
  import { GOOGLE_EVENTS } from '../../googleEvents';
  import Menu from './../../components/menu/Menu.vue';
  import DotsButton from '../../components/buttons/dots-button/DotsButton.vue';
  import Loader from '../../components/loader/Loader.vue';
  import Logo from '../../components/logo/Logo.vue';
  import Promotion from '../../components/promotion/Promotion.vue';

  const registry = Registry.getInstance();
  let promotionService = null;

  export default {
    components: {
      'app-menu': Menu,
      'dots-button': DotsButton,
      'loader': Loader,
      'logo': Logo,
      'promotion': Promotion,
    },
    data() {
      return {
        promotionElements: [],
      };
    },
    async created() {
      promotionService = registry.get(SERVICES.PROMOTION_SERVICE);
      this.promotionElements = await promotionService.getFooter();
    },
    methods: {
      loader() {
        return this.$refs.loader;
      },
      onShowMenu() {
        this.$refs.menu.show();
        this.trackEvent(GOOGLE_EVENTS.MENU__SHOWN);
      },
      onHideMenu() {
        this.$refs.menuShowButton.disable();
        this.trackEvent(GOOGLE_EVENTS.MENU__CLOSED);
      },
      onClickFooter() {
        this.trackEvent(GOOGLE_EVENTS.FOOTER__CLICKED);
      },
      onHoverFooter() {
        this.trackEvent(GOOGLE_EVENTS.FOOTER__HOVER);
      },
      onHoverFooterLink() {
        this.trackEvent(GOOGLE_EVENTS.FOOTER_LINK__HOVER);
      }
    }
  }
</script>