import './setupRegistry';
import bugsnagVue from '@bugsnag/plugin-vue';
import Vue from 'vue';
import VueRouter from 'vue-router';
import { routes } from './routes';
import App from './components/App.vue';
import PerfectScrollbar from 'vue2-perfect-scrollbar';
import 'vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css';
import './less/layout.less';
import { Registry } from './Registry';
import { SERVICES } from './services';
import { PATH } from './routes';
import { noop, isBugsnagEnabled } from '../bg/libs/helpers';

const register = Registry.getInstance();

Vue.use(PerfectScrollbar);
Vue.use(VueRouter);

const router = new VueRouter({ routes });

Vue.mixin({
  methods: {
    openNewTab(text, url) {
      this.trackEvent({
        category: 'openLink',
        action: text,
        label: url
      });
      register.get(SERVICES.TAB_SERVICE).create({ url });
    },
    trackEvent(event) {
      register.get(SERVICES.GOOGLE_ANALYTICS).trackEvent(event.category, event.action, event.label);
    },
  }
});

chrome.runtime.getBackgroundPage(async(backgroundPage) => {
  register.set(SERVICES.BACKGROUND_PAGE, backgroundPage);

  // wait for background staff ready
  await backgroundPage.mario.getService(Symbol.for('URBAN_READY')).promise;

  const urbanLocationService = backgroundPage.mario.getService(Symbol.for('URBAN_LOCATION_SERVICE'));
  const managementService = backgroundPage.mario.getService(Symbol.for('MANAGEMENT'));
  const ipInfoService = backgroundPage.mario.getService(Symbol.for('URBAN_IP_SERVICE'));
  const pageHistoryService = backgroundPage.mario.getService(Symbol.for('URBAN_PAGE_HISTORY_SERVICE'));
  const policyService = backgroundPage.mario.getService(Symbol.for('POLICY_SERVICE'));
  const googleAnalyticsService = backgroundPage.mario.getService(Symbol.for('GOOGLE_ANALYTICS_SERVICE'));
  const urbanBagsnagService = backgroundPage.mario.getService(Symbol.for('URBAN_BAGSNAG_SERVICE'));
  const safeCheckStateService = backgroundPage.mario.getService(Symbol.for('USC_STATE_SERVICE'));
  const adBlocker = backgroundPage.mario.getService(Symbol.for('URBAN_AD_BLOCKER_SERVICE'));
  const extensionModeService = backgroundPage.mario.getService(Symbol.for('URBAN_EXTENSION_MODE_SERVICE'));
  const interruptionService = backgroundPage.mario.getService(Symbol.for('URBAN_INTERRUPTION_SERVICE'));
  const flagService = backgroundPage.mario.getService(Symbol.for('FLAG_SERVICE'));
  const promotionService = backgroundPage.mario.getService(Symbol.for('URBAN_PROMOTION_STORAGE'));

  register.set(SERVICES.MANAGEMENT, managementService);
  register.set(SERVICES.IP_INFO_SERVICE, ipInfoService);
  register.set(SERVICES.LOCATION_SERVICE, urbanLocationService);
  register.set(SERVICES.PAGE_HISTORY_SERVICE, pageHistoryService);
  register.set(SERVICES.POLICY, policyService);
  register.set(SERVICES.GOOGLE_ANALYTICS, googleAnalyticsService);
  register.set(SERVICES.BUGSNAG, urbanBagsnagService);
  register.set(SERVICES.SAFE_CHECK_STATE_SERVICE, safeCheckStateService);
  register.set(SERVICES.AD_BLOCKER, adBlocker);
  register.set(SERVICES.EXTENSION_MODE, extensionModeService);
  register.set(SERVICES.INTERRUPTION_SERVICE, interruptionService);
  register.set(SERVICES.FLAG_SERVICE, flagService);
  register.set(SERVICES.PROMOTION_SERVICE, promotionService);


  if (backgroundPage) {
    new Vue({
      el: '#app',
      router,
      render: h => h(App),
    });
  }

  if (isBugsnagEnabled()) {
    const bugsnagClient = backgroundPage.mario.getService(Symbol.for('BUGSNAG_SERVICE')).getClient();
    bugsnagClient.use(bugsnagVue, Vue);
  }

  await urbanLocationService.ready();

  setTimeout(() => {
    router.push(PATH.MAIN, noop, noop);
  });
});
