<template>
  <div class="dots_button" @click="onClick" v-bind:class="{ 'dots_button--active': isActive }">
    <div class="dots_button__dot dots_button__dot_top"></div>
    <div class="dots_button__dot dots_button__dot_middle"></div>
    <div class="dots_button__dot dots_button__dot_bottom"></div>
  </div>
</template>

<style lang="less" src="./DotsButton.less"></style>

<script>
  const EVENTS = {
    ACTIVATED: 'activated',
  };

  export default {
    data() {
      return {
        isActive: false
      };
    },
    methods: {
      enable() {
        this.isActive = true;
      },

      disable() {
        this.isActive = false;
      },
      toggle() {
        this.isActive = !this.isActive;
      },
      onClick: function () {
        if (this.isActive) {
          return;
        }

        this.isActive = true;
        this.$emit(EVENTS.ACTIVATED);
      },
      diactivate: function () {
        this.isActive = false;
      }
    },

  }
</script>
