<template src="./RateUsPage.html"></template>
<style lang="less" src="./RateUsPage.less"></style>

<script>
  import SimpleLayout from '../../layouts/simple/SimpleLayout.vue';
  import { Registry } from '../../Registry';
  import { PATH } from '../../routes';
  import { SERVICES } from '../../services';
  import { GOOGLE_EVENTS } from '../../googleEvents';

  export default {
    components: {
      'simple-layout': SimpleLayout,
    },
    mounted() {
      this.trackEvent(GOOGLE_EVENTS.RATE_US_PAGE__SHOWN);
      this.$refs.layout.showCloseButton();
      const pageHistoryService = Registry.getInstance().get(SERVICES.PAGE_HISTORY_SERVICE);
      pageHistoryService.visit(PATH.RATE_US);
    },
    data() {
      return {
        browser: process.env.BROWSER_NAME
      }
    },
    methods: {
      rateUs() {
        this.trackEvent(GOOGLE_EVENTS.RATE_US_BUTTON__CLICKED);
        this.openNewTab('Rate us!', process.env.EXTENSION_STORE_URL);
      },
      rateUsHover() {
        this.trackEvent(GOOGLE_EVENTS.RATE_US_BUTTON__HOVERED);
      },
      onClosePage() {
        this.trackEvent(GOOGLE_EVENTS.RATE_US_CLOSE_BUTTON__CLICKED);
        this.$router.push(PATH.MAIN);
      },
    },
  }
</script>