<template src="./SelectionList.html"></template>
<style lang="less" src="./SelectionList.less"></style>

<script>
  const EVENTS = {
    SELECTED: 'select',
  }

  export default {
    props: {
      items: Array,
      selected: String,
      disabled: Boolean
    },
    data() {
      return {
        selectedId: undefined
      };
    },
    mounted() {
    },
    watch: {
      selected: {
        immediate: true,
        handler(value) {
          if (value) {
            this.selectedId = value;
          }
        }
      }
    },
    methods: {
      select(id) {
        if (this.disabled) {
          return;
        }

        this.selectedId = id;

        this.$emit(EVENTS.SELECTED, id);
      }
    }
  }
</script>
