<template src="./SimpleLayout.html"></template>
<style lang="less" src="./SimpleLayout.less"></style>

<script>
  import Logo from '../../components/logo/Logo.vue';
  import CrossIcon from '../../components/icons/cross-icon/CrossButton.vue';
  import Promotion from '../../components/promotion/Promotion.vue';
  import { Registry } from '../../Registry';
  import { SERVICES } from '../../services';

  const registry = Registry.getInstance();
  let promotionService = null;

  const EVENTS = {
    CLOSE_PAGE: 'close'
  };

  export default {
    props: ['hideFooter'],
    components: {
      logo: Logo,
      'cross-icon': CrossIcon,
      'promotion': Promotion,
    },
    data() {
      return {
        closeShown: false,
        promotionElements: [],
      };
    },
    async created() {
      if (this.hideFooter) {
        return;
      }
      promotionService = registry.get(SERVICES.PROMOTION_SERVICE);
      this.promotionElements = await promotionService.getFooter();
    },
    methods: {
      onClose() {
        this.$emit(EVENTS.CLOSE_PAGE);
      },
      showCloseButton() {
        this.closeShown = true;
      },
    },
  }
</script>