<template src="./MainPage.html"></template>
<style lang="less" src="./MainPage.less"></style>

<script>
  import MainLayout from '../../layouts/main/MainLayout.vue';
  import List from '../../components/list/List.vue';
  import PowerButton from '../../components/buttons/power-button/PowerButton.vue';
  import ProtectionLevelBar from '../../components/protection-level-bar/ProtectionLevelBar.vue';
  import { Registry } from '../../Registry';
  import { SERVICES } from '../../services';
  import { MarioEvent } from '@urbandevs/mario-core/dist/src';
  import { ContentEvents } from '../../../bg/ContentEvents';
  import { GOOGLE_EVENTS } from '../../googleEvents';
  import { PATH } from '../../routes';
  import { callExternalEvent, delay } from '../../helpers';

  const registry = Registry.getInstance();
  let adBlocker = null
  let urbanLocationService = null;
  let policyService  = null;
  let dispatcher = null;

  const PROCESSING = 'Processing';

  export default {
    data() {
      return {
        locations: {},
        currentLocation: {},
        adsOnPage: null,
        adsTotal: null,
        protectionEnabled: null,
        protectionLevel: {
          current: 0,
          max: 0
        },
        ipDetails: []
      };
    },

    components: {
      'layout': MainLayout,
      'list': List,
      'power-button': PowerButton,
      'protection-level-bar': ProtectionLevelBar
    },

    created() {
      urbanLocationService = registry.get(SERVICES.LOCATION_SERVICE);
      dispatcher = registry.get(SERVICES.EXTERNAL_DISPATCHER);

      dispatcher.emit(new MarioEvent(ContentEvents.POPUP_COUNT));
    },

    async mounted() {
      adBlocker = registry.get(SERVICES.AD_BLOCKER);
      policyService = registry.get(SERVICES.POLICY);

      this.updateProtectionStateInfo();
      this.updateIpInfo();

      await this.updateStatus();
    },

    methods: {
      mapLocation(location) {
        const code = location.code().value().toLowerCase();
        return {
          name: location.name().value(),
          flag: `/popup/flags/svg/${code}.svg`,
          code: location.code().value(),
        };
      },

      async updateStatus() {
        const state = await callExternalEvent(dispatcher, ContentEvents.POPUP_GET_PROTECTION_STATE);
        this.protectionEnabled = state.enabled;

        const counters = await adBlocker.getAdBlockerCountersForActiveTab();
        this.adsOnPage = counters.currentPageTotal;
        this.adsTotal = counters.currentSessionTotal;

        const method = this.protectionEnabled ? 'turnON' : 'turnOFF';
        this.$refs.power[method](false);
      },

      async onTurnOn() {
        this.$refs.template.loader().show(PROCESSING);

        this.protectionEnabled = true;

        // XXX: policy will be agreed 'asyncly'
        if (!(await policyService.isAgreed())) {
          await policyService.agree();
        }

        await delay(500);

        await callExternalEvent(dispatcher, ContentEvents.POPUP_SET_PROTECTION_STATE, {
          action: 'SET_PROTECTION_STATE',
          enabled: true
        });

        this.updateProtectionStateInfo();

        this.trackEvent(GOOGLE_EVENTS.PROTECTION__ON);

        this.updateIpInfo();

        this.$refs.template.loader().hide();
      },

      async onTurnOff() {
        this.protectionEnabled = false;

        this.$refs.template.loader().show(PROCESSING);

        await callExternalEvent(dispatcher, ContentEvents.POPUP_SET_PROTECTION_STATE, {
          action: 'SET_PROTECTION_STATE',
          enabled: false
        });

        this.trackEvent(GOOGLE_EVENTS.PROTECTION__OFF);
        this.updateIpInfo();
        this.$refs.template.loader().hide();
      },

      updateIpInfo() {
        const ipInfoService = registry.get(SERVICES.IP_INFO_SERVICE);
        const countryCode = ipInfoService.getIpInfo().country;

        this.ipDetails = [
          {
            name: 'Your current IP',
            value: ipInfoService.getIpInfo().ip.ip,
          },
          {
            name: 'ISP',
            value: ipInfoService.getIpInfo().isp,
          },
          {
            name: 'Country',
            value: urbanLocationService.getCountryNameByCode(countryCode),
          }
        ];
      },

      async updateProtectionStateInfo() {
        const protectionState = await callExternalEvent(dispatcher, ContentEvents.POPUP_GET_PROTECTION_STATE);
        this.protectionLevel = protectionState.level;
      }
    },
  }
</script>